

import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


import {
  mapState
} from 'vuex'

export default {

  name: 'app',

  directives: {
    TransferDom
  },

  components: {
    ButtonTab,
    ButtonTabItem,
    XHeader,
    Tabbar,
    TabbarItem,
    Loading
  },

  data () {
    return {
      tabIndex: 0
    }
  },

  computed: {

    ...mapState({
      isLoading: state => state.isLoading,
      direction: state => state.direction
    }),

    headerTransition () {
      return this.direction === 'forward' ? 'header-fade-in-right' : 'header-fade-in-left'
    },

    headerTitle () {
      for (const item of this.tabbarItems) if (this.isTabbarSelected(item.link)) return item.name
    },

    headerOptions () {
      return {
        title: this.$t('nav.title'),
        left: {
          showBack: true,
          backText: this.$t('nav.back')
        },
        right: {
          showMore: true
        }
      }
    },

    tabbarItems () {
      return [
        {
          link: { name: 'index', query: this.$route.query },
          name: this.$t('tab.index')
        },
        {
          link: { path: '/notFound', query: this.$route.query },
          name: '404'
        }
      ]
    }
  },

  methods: {

    scrollToTop () {
      window.scroll(0, 0)
    },

    changeLanguage () {
      this.$i18n.locale = this.tabIndex === 0 ? 'en' : 'zh'
    },

    isTabbarSelected ({ link, name }) {
      return link === this.$route.path || name === this.$route.name
    }

  }

}



