import * as apis from 'src/apis'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {

  name: 'ci-hello',

  /** *********************** mixin  ************************* **/
  // mixins: [HELLO_MIXIN],

  /** *********************** register local components  ************************* **/
  components: {
    ButtonTab,
    ButtonTabItem,
    XButton
  },

  /** ***********************  data from parent component ************************* **/
  props: {
  },

  /** ***********************  local data  ************************* **/
  data () {
    return {
      tabIndex: 0,
      help: 'write in src/components/Hello.vue and save',
      linkList: [
        { href: 'http://router.vuejs.org/', name: 'vue-router' },
        { href: 'http://vuex.vuejs.org/', name: 'vuex' },
        { href: 'http://vue-loader.vuejs.org/', name: 'vue-loader' },
        { href: 'https://github.com/vuejs/awesome-vue', name: 'awesome-vue' }
      ]
    }
  },

  /** ***********************  data after computed  ************************* **/
  computed: {
    titleText () {
      return this.$t('hello.title')
    },
    languageBtnText () {
      return this.$t('hello.languageBtn')
    }
  },

  /** ***********************  life ciycle  ************************* **/

  beforeCreate () {},
  created () {
    this.tabIndex = this.$i18n.locale === 'en' ? 0 : 1
  }, // before dom create

  mounted () {
    // everytime dom render over
    this.$nextTick(_ => {
     // document.getElementsById
    })
  }, // after dom created

  beforeUpdate () {},
  updated () {},
  activated () {},
  deactivated () {},
  beforeDestroy () {},
  destroyed () {},

  /** ***********************  functions  ************************* **/
  methods: {

    async getData () {
      this.$vux.loading.show({
        text: this.$t('loading.text')
      })
      try {
        await apis.fetchLogin({})
      } catch ({ message }) {
        this.$vux.alert.show({
          title: message
        })
      } finally {
        this.$vux.loading.hide()
      }
      // Another way
      // this.$createApi(url, params)
      // .then(res => {
      // })
      // .catch(error => {
      // })
      // also can use $http
      // this.$http.post  || this.$http.get
    }

  },

  /** ***********************  watch for data change  ************************* **/
  watch: {
  }

}



